import React, { useState } from 'react';
import ReactDOM from 'react-dom/client';
import './index.css';
import './App.css';
import reportWebVitals from './reportWebVitals';
import 'bootstrap/dist/css/bootstrap.min.css';
import { Form, Container, Row, Col } from 'react-bootstrap';
import { Chart, registerables } from "chart.js"
import ToggleSexButton from './components/ToggleSexButton'
// import AgeDataList from './components/AgeDataList'

import './components/SdScoreChart.js'
import SdScoreChart from './components/SdScoreChart.js';
import html2canvas from "html2canvas";

Chart.register(...registerables)

const root = ReactDOM.createRoot(document.getElementById('root'));

function App() {
  const [sex, setSex] = useState('male');
  const [age, setAge] = useState('0');
  const [height, setHeight] = useState(0);
  const [weight, setWeight] = useState(0);

  const changeAge = (e) => {
    setAge(e.target.value)
  }

  const changeHeight = (e) => {
    setHeight(e.target.value)    
  }

  const changeWeight = (e) => {
    setWeight(e.target.value)    
  }


  // -------------------------------------------------
  // ダウンロード リンク
  const saveAsImage = uri => {
    const downloadLink = document.createElement("a");
  
    if (typeof downloadLink.download === "string") {
      downloadLink.href = uri;
  
      let d = new Date();
      let year = d.getFullYear();
      let month = d.getMonth() + 1;
      let day = d.getDate();
      let file_name = year + '-' + month + '-' + day + '.png'
      // let file_name = d + '.png'


      // ファイル名
      downloadLink.download = file_name;
  
      // Firefox では body の中にダウンロードリンクがないといけないので一時的に追加
      document.body.appendChild(downloadLink);
  
      // ダウンロードリンクが設定された a タグをクリック
      downloadLink.click();
  
      // Firefox 対策で追加したリンクを削除しておく
      document.body.removeChild(downloadLink);
    } else {
      window.open(uri);
    }
  }

  // -------------------------------------------------

  const onClickExport = () => {
    // 画像に変換する component の id を指定
    const target = document.getElementById("chartArea");
    html2canvas(target).then(canvas => {
      const targetImgUri = canvas.toDataURL("img/png");
      saveAsImage(targetImgUri);
    });
  }



  return (
    <>
    {/* fluid */}
    <Container fluid>
      <Row className='h-100'>
        <Col md={3} className='sideArea'>

          {/* <Row className='mh-10'></Row> */}

          <div className='mt-2'>
            <h5>横断的成長曲線 (0～17才)</h5>
            <button onClick={() => onClickExport()}>PNG出力</button>
          </div>

          <div className='mt-3 ml-5'>
            <ToggleSexButton setSex={setSex}/>
          </div>

          <Form.Group controlId="birthday" className='form-inline mt-3 ml-1 col-xs-12'>
                  {/* <Form.Label>生年月日</Form.Label>
                  <Form.Control type="date" placeholder="生年月日" className='ml-3'/> */}
            <Form.Label>年齢</Form.Label>
            <div className='col-xs-3'>
              <Form.Control type="number" placeholder="年齢" className='ml-3' onChange={changeAge} value={age}/>
              {/* <div className='ml-3'>
                <AgeDataList onChange={changeAge}/>
              </div> */}
              {/* <Form.Select aria-label="Default select example" className='ml-3'>
                <option>Open this select menu</option>
                <option value="1">One</option>
                <option value="2">Two</option>
                <option value="3">Three</option>
              </Form.Select> */}
            </div>
            <Form.Text className='ml-3'>才</Form.Text>
          </Form.Group>

          <Form.Group controlId="height" className='form-inline mt-3 ml-1 col-xs-12' >
            <Form.Label>身長</Form.Label>
            <div className='col-xs-3'>
              <Form.Control type="number" placeholder="身長" className='ml-3' onChange={changeHeight} value={height}/>
            </div>
            <Form.Text className='ml-3'>cm</Form.Text>
          </Form.Group>
          
          <Form.Group controlId="weight" className='form-inline mt-3 ml-1' >
            <Form.Label>体重</Form.Label>
            <div className='col-xs-3'>
              <Form.Control type="number" placeholder="体重" className='ml-3 max-vw-150' onChange={changeWeight} value={weight}/>
            </div>
            <Form.Text className='ml-3'>Kg</Form.Text>
          </Form.Group>



        </Col>
        <Col md={9} >
          <div id='chartArea' className='h-100 chartArea'>
              <SdScoreChart sex={sex} age={age} height={height} weight={weight}/>
          </div>
        </Col>

        </Row>
      </Container>
    </>
  )
}


root.render(
  <App/>
);

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();

