import React, { useState } from 'react';

import { ButtonGroup, ToggleButton } from 'react-bootstrap';
// import { propTypes } from 'react-bootstrap/esm/Image';


function ToggleSexButton(props) {
  const [radioValue, setRadioValue] = useState('male');

  const radios = [
    { name: '男性', value: 'male' },
    { name: '女性', value: 'female' },
  ];

  function changeSex(val) {
    props.setSex(val)
    setRadioValue(val)    
  }

  return (
    <>

      <ButtonGroup className='w-50 ml-2'>
        
        {radios.map((radio, idx) => (
          <ToggleButton
            key={idx}
            id={`radio-${idx}`}
            type="radio"
            variant={idx % 2 ? 'outline-danger' : 'outline-info'}
            name="radio"
            value={radio.value}
            checked={radioValue === radio.value}
            onChange={(e) => changeSex(e.currentTarget.value)}
            // className="-"
          >
            {radio.name}
          </ToggleButton>
        ))}
      </ButtonGroup>
    </>
  );
}

export default ToggleSexButton;